body {
  margin: 0;
  padding: 0;
  min-width: 280px;
  font-family: "Ubuntu-medium", "Arial", sans-serif;
  color: #615D5D;
  font-size: 18px;
  line-height: 1.3;
  font-weight: 400;
}

a {
  text-decoration: none;
}

img {
  max-width: 100%;
  height: auto;
}

input,
textarea,
button {
  font: inherit;
}

.h1, .h2, .h3, .h4, .h5, .h6, h1, h2, h3, h4, h5, h6 {
  overflow: hidden;
  text-overflow: ellipsis;
}

@font-face {
  font-family: "Ubuntu-medium";
  src: url("../fonts/Ubuntu-Medium.woff2") format("woff2"), url("../fonts/Ubuntu-Medium.woff") format("woff");
  font-weight: normal;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Ubuntu-regular";
  src: url("../fonts/Ubuntu-Regular.woff2") format("woff2"), url("../fonts/Ubuntu-Regular.woff") format("woff");
  font-weight: normal;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Ubuntu-light";
  src: url("../fonts/Ubuntu-Light.woff2") format("woff2"), url("../fonts/Ubuntu-Light.woff") format("woff");
  font-weight: normal;
  font-style: normal;
  font-display: swap;
}
.visually-hidden:not(:focus):not(:active),
input[type=checkbox].visually-hidden,
input[type=radio].visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.container {
  padding-left: 17px;
  padding-right: 17px;
  margin: 0 auto;
}

@media (min-width: 768px) {
  .container {
    padding-left: 25px;
    padding-right: 25px;
    max-width: 1600px;
  }
}
@media (min-width: 1600px) {
  .container {
    width: 1600px;
  }
}
.notice__slide {
  position: relative;
  width: 100%;
  height: auto;
  padding-top: 25.4%;
  overflow: hidden;
  background-repeat: no-repeat;
  background-position: center 0;
  background-size: cover;
  background-position: bottom right;
  box-sizing: border-box;
}

.notice__logo {
  display: flex;
  align-items: center;
  position: absolute;
  top: 34px;
  left: 50%;
  color: #ffffff;
  font-family: "Ubuntu-light";
  font-size: 22px;
  line-height: 1.2;
  transform: translateX(-50%);
  cursor: pointer;
}
.notice__logo svg {
  padding: 0 5px;
}
.notice__logo span {
  font-family: "Ubuntu-medium";
}

.notice__slide--green {
  background-image: url("../img/slide1.jpg");
}
.notice__slide--green .notice__logo span {
  color: #0ED984;
}
.notice__slide--green .notice__par {
  color: #0ED984;
}
.notice__slide--green .notice__par::after {
  background-color: #0ED984;
}

.notice__slide--blue {
  background-image: url("../img/slide2.jpg");
}
.notice__slide--blue .notice__logo span {
  color: #3577F6;
}
.notice__slide--blue .notice__par {
  color: #3577F6;
}
.notice__slide--blue .notice__par::after {
  background-color: #3577F6;
}

.notice__slide--pink {
  background-image: url("../img/slide3.jpg");
}
.notice__slide--pink .notice__logo span {
  color: #D72DF3;
}
.notice__slide--pink .notice__par {
  color: #D72DF3;
}
.notice__slide--pink .notice__par::after {
  background-color: #D72DF3;
}

.notice__wrap {
  position: absolute;
  top: 33.5%;
  left: 17px;
}

.notice__par {
  display: inline-block;
  position: relative;
  padding: 0;
  margin-top: 0;
  margin-bottom: 1.4em;
  font-size: 20px;
  line-height: 1.2;
}
.notice__par::after {
  content: "";
  position: absolute;
  bottom: -10px;
  left: 0;
  right: 0;
  height: 3px;
}

.notice__title {
  padding: 0;
  margin-top: 0;
  margin-bottom: 0.46em;
  color: #ffffff;
  font-family: "Ubuntu-medium";
  font-weight: normal;
  font-size: calc(36px + 60 * ((100vw - 375px) / 1225));
  line-height: 1.1;
}

.notice__text {
  padding: 0;
  margin-top: 0;
  margin-bottom: 28px;
  font-family: "Ubuntu-light";
  font-size: 20px;
  line-height: 1.3;
  color: #ffffff;
}

.notice__btn {
  /*position: absolute;
  bottom: 150px;
  left: 50%;
  transform: translateX(-50%);*/
  cursor: pointer;
}

.notice__slider .slick-dots {
  bottom: 46px;
  padding-left: 7px;
  text-align: left;
  box-sizing: border-box;
}

.notice__slider .slick-dots li button {
  height: 15px;
  width: 15px;
  padding: 0;
  border-radius: 50%;
  background-color: transparent;
  border: 1px solid #ffffff;
}
.notice__slider .slick-dots li button:hover {
  border: 1px solid #0ED984;
}

.notice__slider .slick-dots[data-color=green] button:hover {
  border: 1px solid #0ED984;
}

.notice__slider .slick-dots .slick-active button {
  width: 25px;
  height: 25px;
  background-size: cover;
  background-position: center;
  background-color: transparent;
  background-image: url("../img/icon-logo-green.svg");
  border: none;
}
.notice__slider .slick-dots .slick-active button:hover {
  border: none;
}

.notice__slider .slick-dots .slick-active button.dot--green {
  background-image: url("../img/icon-logo-green.svg");
}
.notice__slider .slick-dots .slick-active button.dot--green:hover {
  border: none;
}

.notice__slider .slick-dots .slick-active button.dot--blue {
  background-image: url("../img/icon-logo-blue.svg");
}
.notice__slider .slick-dots .slick-active button.dot--blue:hover {
  border: none;
}

.notice__slider .slick-dots[data-color=blue] button:hover {
  border: 1px solid #3577F6;
}

.notice__slider .slick-dots[data-color=pink] button:hover {
  border: 1px solid #D72DF3;
}

.notice__slider .slick-dots .slick-active button.dot--pink {
  background-image: url("../img/icon-logo-pink.svg");
}
.notice__slider .slick-dots .slick-active button.dot--pink:hover {
  border: none;
}

.notice__slider .slick-dots li, .notice__slider .slick-dots li button::before {
  height: 15px;
  width: 15px;
  margin: 0 12px;
}

@media (min-width: 400px) {
  .notice__slide {
    padding-top: 17.8%;
  }
  .notice__wrap {
    top: 20.5%;
  }
}
@media (min-width: 550px) {
  .notice__slide {
    padding-top: 14.8%;
  }
  .notice__wrap {
    top: 25.5%;
  }
}
@media (min-width: 600px) {
  .notice__slide {
    padding-top: 12.8%;
  }
  .notice__wrap {
    top: 25.5%;
  }
}
@media (min-width: 768px) {
  .notice__slide {
    padding-top: 10.5%;
    background-position: center;
  }
  .notice__slide--green {
    background-image: url("../img/slide1-desktop.jpg");
  }
  .notice__slide--blue {
    background-image: url("../img/slide2-desktop.jpg");
  }
  .notice__slide--pink {
    background-image: url("../img/slide3-desktop.jpg");
  }
  .notice__wrap {
    left: 13.5%;
    right: 13.5%;
    top: 23.5%;
    outline: 1px solid red;
  }
  .notice__slider .slick-next, .notice__slider .slick-prev {
    width: 30px;
    height: 30px;
    background-position: center;
    background-repeat: no-repeat;
    background-color: #fff;
    border-radius: 50%;
    opacity: 0.5;
  }
  .notice__slider .slick-next::before, .notice__slider .slick-prev::before {
    content: "";
  }
  .notice__slider .slick-next:hover, .notice__slider .slick-prev:hover {
    width: 50px;
    height: 50px;
    cursor: pointer;
    background-image: url("../img/icon-arrow-hover.svg");
  }
  .notice__slider .slick-next:active, .notice__slider .slick-prev:active {
    width: 50px;
    height: 50px;
    cursor: pointer;
    background-image: url("../img/icon-arrow-hover.svg");
  }
  .notice__slider .slick-prev {
    left: 65px;
    background-image: url("../img/icon-arrow.svg");
    background-color: transparent;
    z-index: 10;
  }
  .notice__slider .slick-next {
    right: 65px;
    background-color: transparent;
    background-image: url("../img/icon-arrow.svg");
    top: calc(50% - 10px);
    transform: rotate(180deg);
    z-index: 10;
  }
  .notice__slider .slick-next:hover {
    top: calc(50% - 20px);
  }
  .notice__slider .slick-dots {
    padding-left: 13.5%;
  }
  .notice__logo {
    left: 13.5%;
    transform: none;
  }
}
@media (min-width: 1024px) {
  .notice__slide {
    padding-top: 9.8%;
  }
  .notice__title {
    max-width: 850px;
  }
  .notice__text {
    max-width: 550px;
  }
  .notice__btn {
    position: absolute;
    bottom: 0;
    right: 0;
  }
}
@media (min-width: 1600px) {
  .notice__slide {
    padding-top: 0;
    width: auto;
    height: 960px;
  }
  .notice__logo {
    position: relative;
    margin: 0 auto;
    width: 1400px;
    left: 0;
    top: 69px;
  }
  .notice__wrap {
    position: relative;
    width: 1400px;
    margin: 0 auto;
    left: 0;
    right: 0;
    top: 27.5%;
  }
  .notice__text {
    max-width: 580px;
  }
  .notice__title {
    font-size: 96px;
    max-width: 900px;
  }
  .notice__par {
    margin-bottom: 37px;
  }
  .notice__slider .slick-dots {
    position: relative;
    width: 1420px;
    margin: 0 auto;
    bottom: 164px;
    padding-left: 0;
  }
}
.button {
  display: inline-block;
  border: 2px dashed #ffffff;
  font: inherit;
  cursor: pointer;
  text-decoration: none;
  min-width: 140px;
  border-radius: 5px;
  padding: 15px 50px 17px;
  font-size: 20px;
  line-height: 1.2;
  text-align: center;
  color: #ffffff;
  background-color: transparent;
}
.button:hover {
  background-color: #ffffff;
  border: 2px solid #ffffff;
  transition: all 0.3s;
}
.button:active {
  background-color: #ffffff;
  border: 2px solid #ffffff;
  transition: all 0.3s;
}

.button--green:hover {
  color: #0ED984;
}
.button--green:active {
  color: #0ED984;
}

.button--blue:hover {
  color: #3577F6;
}
.button--blue:active {
  color: #3577F6;
}

.button--pink:hover {
  color: #D72DF3;
}
.button--pink:active {
  color: #D72DF3;
}

.advantages__list {
  list-style: none;
  margin: 0;
  padding: 0;
  padding-top: 10px;
  padding-bottom: 47px;
}

.advantages__title {
  font-size: calc(26px + 10 * ((100vw - 375px) / 1225));
  line-height: 1.15;
  padding: 0;
  margin-top: 0;
  margin-bottom: 0.35em;
}
.advantages__title span {
  display: block;
}

.advantages__item {
  position: relative;
  padding-top: 106px;
  padding-bottom: 30px;
  padding-right: 16px;
  border-bottom: 1px solid #F2F2F2;
}
.advantages__item::before {
  content: "";
  position: absolute;
  top: 35px;
  left: 0;
  width: 50px;
  height: 50px;
  background-size: cover;
  background-position: center;
}
.advantages__item:last-child {
  border-bottom: none;
}

.advantages__item--green .advantages__title span {
  color: #0ED984;
}
.advantages__item--green::before {
  background-image: url("../img/icon-dev1.svg");
}

.advantages__item--blue .advantages__title span {
  color: #3577F6;
}
.advantages__item--blue::before {
  background-image: url("../img/icon-dev2.svg");
}

.advantages__item--pink .advantages__title span {
  color: #D72DF3;
}
.advantages__item--pink::before {
  background-image: url("../img/icon-dev3.svg");
}

.advantages__text {
  padding: 0;
  margin: 0;
  font-size: 18px;
  line-height: 1.2;
  font-family: "Ubuntu-light";
}

@media (min-width: 768px) {
  .advantages__list {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    margin: 0 -25px;
  }
  .advantages__item {
    padding-left: 25px;
    padding-right: 25px;
    width: 33.3%;
    box-sizing: border-box;
    border-bottom: none;
    border-right: 1px solid #f2f2f2;
  }
  .advantages__item:nth-child(3), .advantages__item:nth-child(3n) {
    border-right: none;
  }
  .advantages__item::before {
    left: 25px;
  }
  .advantages__text {
    font-size: 20px;
  }
}
@media (min-width: 1600px) {
  .advantages__list {
    padding-top: 135px;
    margin-bottom: 125px;
  }
  .advantages__item::before {
    width: 75px;
    height: 75px;
    top: 5px;
  }
  .advantages__title {
    margin-bottom: 0.65em;
  }
}
.form-section {
  padding-top: 75px;
  padding-bottom: 41px;
  background-color: #f3f3f3;
}

.form-section__title {
  font-size: calc(36px + 30 * ((100vw - 375px) / 1225));
  line-height: 1.15;
  padding: 0;
  margin-top: 0;
  margin-bottom: 0.41em;
}

.form-section__text {
  padding: 0;
  margin-top: 0;
  margin-bottom: 30px;
  font-size: 18px;
  line-height: 1.3;
  letter-spacing: 0.05em;
  font-family: "Ubuntu-light";
}

.form-section__add {
  color: #A6A6A6;
  padding: 0;
  margin: 0;
  font-size: 16px;
  line-height: 1.3;
  font-family: "Ubuntu-light";
  text-align: center;
}
.form-section__add span {
  position: relative;
}
.form-section__add span::before {
  content: "";
  position: absolute;
  bottom: 1px;
  left: 0;
  right: 0;
  height: 1px;
  background-color: #A6A6A6;
}

.form-section__form {
  margin-bottom: 30px;
}

@media (min-width: 768px) {
  .form-section {
    padding-bottom: 80px;
  }
  .form-section__wrap {
    display: grid;
    grid-template-columns: 1fr 1fr;
  }
  .form-section__title {
    order: 1;
  }
  .form-section__form {
    order: 2;
  }
  .form-section__text {
    order: 3;
  }
  .form-section__add {
    order: 4;
    align-self: start;
  }
}
@media (min-width: 1280px) {
  .form-section__wrap {
    grid-template-columns: 1fr 1fr;
    align-items: end;
  }
}
@media (min-width: 1600px) {
  .form-section {
    padding-bottom: 123px;
  }
  .form-section__wrap {
    grid-template-columns: 1fr 820px;
  }
  .form-section {
    padding-top: 150px;
  }
  .form-section__title {
    font-size: 66px;
    margin-bottom: 5px;
  }
  .form-section__text {
    padding-top: 20px;
    max-width: 430px;
  }
}
.form__text {
  padding-left: 15px;
  height: 65px;
  font-family: "Ubuntu-regular";
  color: #1D1A1A;
  font-size: 20px;
  line-height: 65px;
  border: 1px solid #ffffff;
  width: 100%;
  box-sizing: border-box;
  margin-bottom: 16px;
}
.form__text::placeholder {
  color: #C5C5C5;
}
.form__text:hover::placeholder {
  color: #999999;
}
.form__text:active::placeholder {
  color: #999999;
}

.form__send {
  display: inline-block;
  border: none;
  font: inherit;
  cursor: pointer;
  text-decoration: none;
  min-width: 140px;
  width: 100%;
  padding: 20px 20px;
  font-size: 20px;
  line-height: 1.2;
  text-align: center;
  color: #ffffff;
  background-color: #138F5B;
  box-sizing: border-box;
}
.form__send:hover {
  background-color: #04643C;
  transition: all 0.3s;
}
.form__send:active {
  background-color: #04643C;
  transition: all 0.3s;
}

@media (min-width: 1280px) {
  .form {
    display: grid;
    grid-template-columns: 1fr 1fr;
    column-gap: 20px;
  }
  .form__send {
    width: auto;
    grid-column: 1/2;
  }
}
@media (min-width: 1600px) {
  .form {
    display: grid;
    grid-template-columns: 1fr 1fr 220px;
    grid-template-rows: auto;
    justify-content: space-between;
    column-gap: 20px;
    align-items: end;
  }
  .form__text {
    margin-bottom: 0;
  }
  .form__send {
    grid-column: 3/4;
    grid-row: auto;
  }
}
.footer {
  background-color: #1D1A1A;
  padding-top: 18px;
  padding-bottom: 18px;
}

.footer__logo {
  width: 196px;
  margin: 0 auto;
  display: flex;
  align-items: center;
  font-size: 22px;
  line-height: 1.2;
  color: #ffffff;
  cursor: pointer;
  font-family: "Ubuntu-light";
}
.footer__logo svg {
  padding: 0 10px;
}
.footer__logo span {
  color: #0ED984;
  font-family: "Ubuntu-medium";
}

@media (min-width: 768px) {
  .footer__logo {
    margin: 0;
  }
}